import React from 'react';

class FileEmpat extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            nama: 'Adi Fauzi',
            skpd: 'Dinas Pangan Kabupaten Karawang',
            github: ' '
        }
    }

    ubahData(e) {
        this.setState({ github: 'Github Adi Fauzi' })
    }

    render() {
        return (
            <div className="content">
                <h2>=============== Tugas React JS ===============</h2>
                <h3>&copy; {this.state.nama}</h3>
                <h3>{this.state.skpd}</h3>
                <a href="https://github.com/ADI-Netizen/" target="_blank" rel="noopener noreferrer">{this.state.github}</a><br /><br />
                <button onClick={(e) => this.ubahData(e)}>Tampilkan link github saya</button>
            </div>
        )
    }
}

export default FileEmpat;