import React, { Component } from 'react';

class FileTiga extends Component {
    constructor(props) {
        super(props)
        this.state = {
            pesanDariState: 'Klik Tombol di Bawah ini',
        }

    }

    ubahDataState(isi) {
        this.setState({ pesanDariState: "Bangsa yang tidak percaya kepada kekuatan dirinya sebagai suatu bangsa, tidak dapat berdiri sebagai suatu bangsa yang merdeka" })
    }

    render() {
        return (
            <div className="content">
                <h2>Pesan Bung Karno</h2>
                <p>{this.state.pesanDariState}</p>
                <button onClick={(isi) => this.ubahDataState(isi)}>Tampilkan Pesan</button>
            </div>
        )
    }
}

export default FileTiga;